.new-expense {
    background-color: aliceblue;
    padding: 1rem;
    margin: 2rem auto;
    width: 50rem;
    max-width: 90%;
    border-radius: 12px;
    text-align: center;
    box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
}

.new-expense button {
    font: inherit;
    cursor: pointer;
    padding: 1rem 2rem;
    border: 1px solid#40005d;
    background-color: #40005d;
    color: white;
    border-radius: 12px;
    margin-right: 1rem;
}

.new-expense button:hover,
.new-expense button:active {
    background-color: #510674;
    border-color: #510674;
}

.new-expense button.alternativ {
    color: #220131;
    border-color: #220131;
}

.new-expense button.alternativ:hover,
.new-expense button.alternativ:active {
    background-color: #ddb3dd;
}

@media (max-width:768px) {
    .new-expense button {
        margin: 1rem;
    }
}